.form {
    border-radius: 15px;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-around;
    width: 320px;
    height: 300px;
    margin: 40px auto;
    padding: 30px;
    background: #FFB273;
    box-shadow: 1px 1px 20px gray, -1px 1px 20px gray, 1px -1px 20px gray, -1px -1px 20px gray;
}

.form input {
    border-radius: 5px;
    width: 90%;
    height: 25px;
    border: none;
}

.form button {
    width: 200px;
    height: 40px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

.form button:hover {
    background: gray;
    transition: .3s ease;
}

.form_hide {
    display: none;
}

.status {
    display: none;
}

.status_visible {
    display: block;
    text-align: center;
    margin-top: 100px;
    font-size: 35px;
}

.title {
    text-align: center;
    margin-top: 30px;
    letter-spacing: 3px;
    font-size: 35px;
    color: gray;
}